.row{
   color: white;
   margin:20px;

}
.row__posters{
  display:flex;
  overflow-y:hidden;
  overflow-x:scroll;
  padding:20px;
}

.row__posters::-webkit-scrollbar{
    display:none;
}

.row__poster{
    max-height:100px;
    object-fit: contain;
    margin-right:10px;
    width:100%;
    transiton: transform 450ms;

}
.row__poster:hover{
    transform:scale(1.08);
    opacity:1;
}
.row_posterLarge{
    max-height:250px;
}
.row_posterLarge:hover{
    transform:scale(1.09);
    opacity:1;
}